.header {
	position: absolute;
	top: 0;
	left: 25%;
	right: 25%;
	height: 100px;
	text-align: center;
	z-index: 11; }
.logo {
	position: absolute;
	top: 0;
	left: 50%;
	transform: translate(-50%, 0);
	transition: transform 400ms;
	a {
		display: block;
		color: $text-color-dark;
		p {
			display: none;
			font-size: 2.4rem;
			text-transform: uppercase;
			opacity: 0;
			//visibility: hidden
			transition: opacity 400ms; }
		svg {
			font-size: 10rem;
			transform: scale(0.4);
			transition: transform 400ms; } }
	.is-intro & {
		transform: translate(-50%, 180px);
		p {
			display: block;
			opacity:  1;
 }			//visibility: visible
		svg {
			transform: scale(1); } } }
