// common styles here
$main: #1b223b;
$dark: #161b2f;
$text-color: #8996be;
$text-color-dark: #3a4776;

html {
	font-size: 10px;
	@include rh(767) {
		font-size: 9px; } }
body {
	background: $main;
	color: $text-color;
	font-family: 'Montserrat', sans-serif;
	font-size: 1.6rem;
	line-height: 1.5; }
.out {
	position: relative;
	height: 100%;
	min-height: 700px; }
